// Store Theme Wrapper - 스토어 모듈 전체에 테마 적용
.store-theme-wrapper {
  // 스토어 모듈의 accent 색상을 오렌지 계열로 오버라이드
  --accent-primary: #ff6b35;
  --accent-secondary: #e55a2b;
  --accent-color: #ff6b35;
  --primary-color: #ff6b35;
  --primary-hover-color: #e55a2b;
  
  // 다크 테마일 때
  &.dark-theme {
    --accent-primary: #00d4aa;
    --accent-secondary: #00b894;
    --accent-color: #00d4aa;
    --primary-color: #00d4aa;
    --primary-hover-color: #00b894;
  }
}

.store-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #f5f5f5;
  
  // iOS에서 스크롤 바운스 효과 제거
  overscroll-behavior-y: contain;
  -webkit-overflow-scrolling: touch;
  
  // iOS Safari에서 100vh 문제 해결
  @supports (-webkit-touch-callout: none) {
    height: -webkit-fill-available;
  }
}

.store-content {
  min-height: 100%;
  background-color: white;
  position: relative;
  
  // iOS에서 스크롤 영역 명확히 설정
  touch-action: pan-y;
}